import * as Yup from "yup";
import React from "react";
import {ErrorMessage} from "../../component/common/FormComponent/FormComponent";

const setSettingCreator = (min, max) => Yup.string().min(min, <ErrorMessage/>).max(max, <ErrorMessage/>).required(<ErrorMessage/>);
const setStartCreator   = setting => Yup.object().shape(setting);

export const setStatusValidation        = setStartCreator({status: setSettingCreator(1, 50)});
export const setPostValidation          = setStartCreator({postText: setSettingCreator(1, 50)});
export const setMessageValidation       = setStartCreator({messageText: setSettingCreator(1, 50)});
export const setLoginValidation         = setStartCreator({
    email: setSettingCreator(1, 50),
    password: setSettingCreator(1, 50),
    captcha: setSettingCreator(1, 50),
});
export const setProfileDataValidation   = setStartCreator({
    fullName: setSettingCreator(1, 50),
    aboutMe: setSettingCreator(1, 50),
    lookingForAJob: setSettingCreator(1, 50),
    lookingForAJobDescription: setSettingCreator(1, 50),
    vk: setSettingCreator(1, 50),
    facebook: setSettingCreator(1, 50),
    github: setSettingCreator(1, 50),
});

