const setError  = "error/SET ERROR";

const errorInit = {
    error: null
};

export const errorReducer = (state = errorInit, action) => {
    const newState = {...state};

    switch (true) {
        case setError === action.type:
            newState.error = action.error;
            return newState;
        default: return state;
    };
};

export const setErrorActionCreator = error => {
    return {type: setError, error: error};
};