import {connect} from "react-redux";
import {LoginForm} from "./LoginForm";
import {getImageSelect, getUserIsNotBotSelect} from "../../../utils/reselect/reselect";
import {getCaptchaThunkCreator} from "../../../state/reducers/authReducer";

const mapStateToProps = state => {
    return {
        image: getImageSelect(state),
        userProcessVerify: getUserIsNotBotSelect(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getCaptcha: IsUserProcessVerify => {
            dispatch(getCaptchaThunkCreator(IsUserProcessVerify));
        }
    };
};

export const LoginConteiner = connect(mapStateToProps, mapDispatchToProps)(LoginForm);