import React from "react";

export const LoginFormButton = ({image, getCaptcha, callChangeUserProcessVerifyAndGetCaptcha}) => {
    if (image) {
        return <button type={"button"} onClick={getCaptcha}>Refresh</button>;
    };

    return  <button type={"button"} onClick={callChangeUserProcessVerifyAndGetCaptcha}>I'm not bot</button>;
};