import React, {useState} from "react";
import style from "../login.module.scss";
import {ErrorMessage, Field, Form, Formik} from "formik";
import {setLoginValidation} from "../../../utils/validate/validate";
import {LoginFormButton} from "./loginFormButton/LoginFormButton";

export const LoginForm = ({onSubmit, image, userProcessVerify, getCaptcha}) => {

    return (
        <Formik validationSchema={setLoginValidation} initialValues={{email: "", password: "", captcha: "", rememberMe: false}} onSubmit={onSubmit}>
            <Form className={style.form}>
                <div>
                    <Field component={"input"} name={"email"} className={style.input}/>
                    <ErrorMessage name={"email"}/>
                </div>
                <div>
                    <Field component={"input"} name={"password"} type={"password"} className={style.input}/>
                    <ErrorMessage name={"password"}/>
                </div>
                <LoginFormButton image={image} getCaptcha={getCaptcha} callChangeUserProcessVerifyAndGetCaptcha={() => getCaptcha(true)} />
                {image ? <img src={image} alt="" style={{width: "200px", height: "200px"}}/> : null}
                <ErrorMessage name={"captcha"}/>
                {image ? <Field name={"captcha"}/> : null}
                {userProcessVerify ? <button type={"submit"}>Send</button> : null}
            </Form>
        </Formik>
    );
};